.modal {
  background-color: rgba(14, 13, 13, 0.4);
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}
.modalActive {
  opacity: 1;
  pointer-events: all;
}
.modalContant {
  padding: 20px;
  background-color: white;
  transform: scale(0.5);
  transition: 0.4s all;
  width: 50vw;
}
.modalContantactive {
  transform: scale(1);
}
